import { useQuery } from '@tanstack/react-query';
import {
  ApiType,
  AttributeDefinition,
  AttributeDefinitions,
  AttributeFile,
  Client,
} from '@youfoundation/transit-lib';
import useAuth from '../auth/useAuth';

export interface AttributeVm extends AttributeFile {
  typeDefinition: AttributeDefinition;
}

const useAttributes = ({ profileId, sectionId }: { profileId: string; sectionId: string }) => {
  const { getSharedSecret } = useAuth();
  const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });
  const fetchData = async (profileId: string, sectionId: string) => {
    const foundAttributes = await client.profileDataProvider.getProfileAttributes(
      profileId,
      sectionId,
      100
    );

    return foundAttributes.map((attr) => {
      return {
        ...attr,
        typeDefinition: Object.values(AttributeDefinitions).find((def) => {
          return def.type.toString() === attr.type;
        }),
      } as AttributeVm;
    });
  };

  return [
    useQuery(['attributes', profileId, sectionId], () => fetchData(profileId, sectionId), {
      refetchOnMount: false,
      refetchOnWindowFocus: false,
    }),
  ] as const;
};

export default useAttributes;
